'use client';

import { useSession, signOut } from 'next-auth/react';
import Link from 'next/link';

export default function Navbar() {
    const { data: session, status } = useSession();

    return (
        <nav className="bg-gray-800 text-white px-6 py-4">
            <div className="max-w-7xl mx-auto flex justify-between items-center">
                <div className="font-bold text-xl">Uotel</div>

                <div className="space-x-4">
                    <Link href="/" className="hover:text-gray-400">Home Page</Link>
                    <Link href="/booking" className="hover:text-gray-400">Booking Room</Link>
                    <Link href="#" className="hover:text-gray-400">Placeholder</Link>
                    <Link href="#" className="hover:text-gray-400">Placeholder</Link>
                    <Link href="#" className="hover:text-gray-400">Placeholder</Link>
                    <Link href="/news" className="hover:text-gray-400">News</Link>
                    <Link href="/contact" className="hover:text-gray-400">Contact Us</Link>
                </div>

                <div>
                    {status === 'loading' && (
                        <span>Loading...</span>
                    )}

                    {status !== 'loading' && session && (
                        <div className="flex items-center gap-3">
                            <span>Welcome, {session.user?.name}</span>
                            <button
                                onClick={() => signOut()}
                                className="bg-red-600 hover:bg-red-700 px-3 py-1 rounded-md"
                            >
                                Logout
                            </button>
                        </div>
                    )}

                    {status !== 'loading' && !session && (
                        <div className="space-x-2">
                            <Link href="/login" className="bg-blue-500 hover:bg-blue-600 px-3 py-1 rounded-md">
                                Login
                            </Link>
                            <Link href="/register" className="bg-green-500 hover:bg-green-600 px-3 py-1 rounded-md">
                                Register
                            </Link>
                        </div>
                    )}
                </div>
            </div>
        </nav>
    );
}
